<template>
	<div class="box">
		<div class="login-box">
			<div class="top-title">车驾管业务自助服务系统</div>
			<div class="center">
				<div class="left"><img src="../assets/image/logos.png" alt=""><div class="text">车驾管服务系统</div></div>
				<div class="right">
					<h3>用户登录</h3>
					<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm">
						<el-form-item prop="username">
							<el-input placeholder="账号" v-model.number="ruleForm.username"></el-input>
						</el-form-item>
						<el-form-item prop="password">
							<el-input type="password" @keyup.enter.native="submitForm('ruleForm')" placeholder="密码" v-model="ruleForm.password" autocomplete="off"></el-input>
						</el-form-item>
						<el-form-item>
							<el-row :gutter="10">
								<el-col :span="12">
									<el-button style="width:100%" @click="zhuce">注册</el-button>
								</el-col>
								<el-col :span="12">
									<el-button :loading="loading" type="primary" style="width:100%" @click="submitForm('ruleForm')">登录</el-button>
								</el-col>
							</el-row>
						</el-form-item>
					</el-form>
				</div>
			</div>
			<div class="footer">
				Copyright ©2018 booky, All Rights Reserved.
			</div>
		</div>
		<el-dialog title="注册" width="35%" :visible.sync="dialogVisible">
			<el-form>
				<el-form-item label="用户名" prop="user">
					<el-input type="text" v-model="ruleForm1.user" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="密码" prop="pass">
					<el-input type="password" v-model="ruleForm1.pass" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="确认密码" prop="checkPass">
					<el-input type="password" v-model="ruleForm1.checkPass" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="姓名" prop="name">
					<el-input v-model.number="ruleForm1.name"></el-input>
				</el-form-item>
				<el-form-item label="身份证号码" prop="id">
					<el-input type="text" v-model.number="ruleForm1.id"></el-input>
				</el-form-item>
				<el-form-item label="员工编号" prop="number">
					<el-input v-model.number="ruleForm1.number"></el-input>
				</el-form-item>
				<el-form-item label="权限验证" prop="key">
					<el-input type="password" v-model.number="ruleForm1.key"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="register">注 册</el-button>
			</div>
		</el-dialog>
		<!-- 密码重置 -->
		<!-- <el-dialog title="密码重置" width="35%" :visible.sync="dialogVisible1">
			<el-form>
				<el-form-item label="用户名" prop="user">
					<el-input disabled type="text" v-model="ruleForm1.user" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="原密码" prop="pass">
					<el-input type="password" v-model="ruleForm1.pass" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="新密码" prop="pass">
					<el-input type="password" v-model="ruleForm1.pass" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="确认密码" prop="checkPass">
					<el-input type="password" v-model="ruleForm1.checkPass" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="register">重 置</el-button>
			</div>
		</el-dialog> -->
	</div>
</template>
<style scoped>
	.box {
		background: url(../assets/image/bj.jpg) no-repeat 50%/cover;
		width: 100%;
		height: 100%;
	}

	.login-box {
		width: 547px;
		height: 100%;
		margin: 0 auto;
		position: relative;
		overflow: hidden;
		font-weight: 600;
	}
	.login-box>.top-title{
		width: 100%;
		text-align: center;
		margin-top: 80px;
		color: #fff;
		font-size: 30px;
	}
	.center {
		width: 100%;
		height: 226px;
		position: absolute;
		top: 0;
		bottom: 0;
		margin: auto 0;
	}

	.left {
		width: 240px;
		height: 100%;
		border-right: 1px solid hsla(0, 0%, 100%, .2);
		float: left;
	}

	.left img {
		margin-top: 20px;
		width: 100%;
	}
	.left>.text{
		color: #fff;
		font-size: 25px;
		text-align: center;
		margin-top: -30px;
	}

	.right {
		width: 280px;
		margin-left: 26px;
		height: 100%;
		float: left;
	}

	.right h3 {
		color: #fff;
		font-size: 16px;
		line-height: 40px;
		font-weight: 400;
	}


	.el-input {
		width: 280px;
	}

	

	.footer {
		width: 100%;
		height: 50px;
		position: absolute;
		bottom: 0;
		line-height: 50px;
		text-align: center;
		color: hsla(0, 0%, 100%, .5);
		font-size: 12px;
	}
</style>
<style>
	label.el-form-item__label{
		width: 120px;
		text-align: center;
	}
</style>
<script>
	import md5 from 'js-md5';
	const axios = require("axios");
	import Qs from 'qs';
	export default {
		data() {
			return {
				ruleForm: {
					username: '',
					password: '',
				},
				ruleForm1: {
					checkPass: '',
					pass: '',
					key:'',
					user:'gao_970707',
					name:'',
					id:'',
					number:''
				},
				rules: {
					username: [{
						required: true,
						message: "用户名不能为空",
						trigger: "blur"
					}],
					password: [{
						required: true,
						message: "密码不能为空",
						trigger: "blur"
					}]
				},
				flag:true,
				loading:false,
				dialogVisible:false,
				dialogVisible1:true
			};
		},
		methods: {
			submitForm(formName) {
				if(this.flag){
					this.$refs[formName].validate((valid) => {
					if (valid) {
						this.flag = false
						this.loading = true
						axios({
							method: "post",
							url: http+"api/ExamineWeb/Login",
							headers: {
								'Content-Type': 'application/x-www-form-urlencoded'
							},
							data: Qs.stringify({
								UserName: this.ruleForm.username,
								Password: md5(this.ruleForm.password)
							})
						})
						.then(res => {
							console.log(res);
							if (res.data.IsSuccess) {
								this.$message({
									showClose: true,
									message: "登陆成功",
									type: "success"
								});
								sessionStorage.setItem(
									"SessionKey",
									res.data.Data.SessionKey
								);
								sessionStorage.setItem(
									"UserName",
									res.data.Data.UserName
								);
								sessionStorage.setItem(
									"Name",
									res.data.Data.Name
								);
								sessionStorage.setItem(
									"LastLoginTime",
									res.data.Data.LastLoginTime
								);
								sessionStorage.setItem(
									"RoleID",
									res.data.Data.RoleID
								);
								this.$router.push({
									name: "index"
								});
							} else {
								this.$message({
									showClose: true,
									message: res.data.Message,
									type: "error"
								});
							}
							this.flag = true
							this.loading = false
						})
						.catch(error => {
							this.$message({
								showClose: true,
								message: "服务器错误",
								type: "error"
							});
							this.flag = true
							this.loading = false
						});
					} else {
						this.$message({
							showClose: true,
							message: "登录失败",
							type: "error"
						});
					}
				});
				}
			},
			zhuce(){
				this.dialogVisible = true
			},
			register(){
				this.$message({
							showClose: true,
							message: "注册权限验证不通过",
							type: "info"
						});
			}
		}
	}
</script>
